import { RendererWrapper } from '..';
import { Deltatime } from '../../../../lib';

import { SceneWrapper } from '../../SceneWrapper';

export class Renderer {
    rendererWrapper: RendererWrapper;
    isActive: boolean = true;

    msLastFrame: number = 0;
    msFpsLimit: number = 30;

    deltatime: Deltatime;

    ctx: CanvasRenderingContext2D;

    sceneWrapper: SceneWrapper;

    constructor(rendererWrapper: RendererWrapper) {
        this.rendererWrapper = rendererWrapper;
        this.Init();
    }
    Init() {
        this.ctx = this.rendererWrapper.graphicsCoreWrapper.canvasWrapper.canvas.ctx;
        this.deltatime = new Deltatime();
        this.Loop();
    }
    Loop() {
        if (!this.isActive) { return; }
        requestAnimationFrame(() => { this.Loop(); });
        this.OnRender();

    }
    OnRender() {
        this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
        this.deltatime.UpdateFPS();
        if (this.sceneWrapper) { this.sceneWrapper.Render(this.ctx); }
    }
    Stop() {
        this.isActive = false;
    }
}